.modal .overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: #000000a3;
    display: flex;
    justify-content: center;
    z-index: 1
    /* align-items: center; */
}
.modal .overlay .content {
    background-color: #fff;
    color: #000;
    padding: 20px 30px;
    border-radius: 10px;
    height: fit-content;
    margin-top: 50px;
}