.login {
    width: 100%;
    height: 100vh;
    background-color: #13213e;
}
.login form {
    position: absolute;
    top: 35%;
    right: 150px;
    transform: translateY(-50%);
    color: #d4d7ff;
}
.login form .login-header {
    display: flex;
    justify-content: center;
    align-items: center;
}
.login form .login-header .logo {
    width: 50px;
    height: 53px;
    margin-right: 7px;
}
.login form label {
    display: block;
    margin: 10px 0;
}
.login form input {
    display: block;
    background: transparent;
    color: #d4d7ff;
    outline: none;
    border: 1px solid #d4d7ff;
    padding: 10px 11px;
    width: 300px;
    border-radius: 5px;
}
.login form button {
    display: block;
    padding: 8px 10px;
    background-color: transparent;
    color: #d4d7ff;
    border: 1px solid #d4d7ff;
    border-radius: 3px;
    cursor: pointer;
    width: 44%;
    margin: 25px auto 0;
}
.login form button:hover {
    background-color: #1a2d54;
}